:root {
  /* Basic colors
  ----------------------------------------------- */
  --color-primary:          #c82986;
  --color-accent:           #3279A6;
  --color-black:            hsl(0, 0%, 0%);
  --color-grey-dark:        hsl(0, 0%, 30%);
  --color-grey:             hsl(0, 0%, 70%);
  --color-grey-light:       hsl(0, 0%, 85%);
  --color-grey-extra-light: hsl(0, 0%, 93%);
  --color-white:            hsl(0, 0%, 100%);
  /* Context colors
  ----------------------------------------------- */
  --color-text:             hsl(0, 0%, 15%);
  --color-text-light:       hsl(0, 0%, 52.5%);
  --color-link:             var(--color-primary);
  --color-border:           var(--color-grey-light);
  --color-success:          #98C93C;
  --color-error:            #D95757;
  /* Typography
  ----------------------------------------------- */
  --font-weight-heavy:  900;
  --font-weight-bold:   700;
  --font-weight-normal: 400;
  --font-weight-light:  300;
  --font-default: "Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
  --font-title: var(--font-default);
  /* Spacing
  ----------------------------------------------- */
  --spacing-xl:   6rem;
  --spacing-l:    3rem;
  --spacing-m:    1.5rem;
  --spacing-s:    1rem;
  --spacing-xs:   .75rem;
  --spacing-xxs:  .5rem;
  --width-xl:     90rem;
  --width-l:      76rem;
  --width-m:      45rem;
  --width-s:      40rem;
  --width-content-m: var(--width-m); }

