    margin-bottom: 1.5rem;
    margin-left: auto;
    margin-right: auto;
    max-width: var(--width-content-m); }
  .blocks .alignfull {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    margin-left: calc(-1 * var(--spacing-m));
    margin-right: calc(-1 * var(--spacing-m));
